@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin onTablet {
  @media (min-width: $tablet-min-width) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: $desktop-min-width) {
    @content;
  }
}

@mixin contentPaddingInline () {
  padding-inline: 30px;

  @include onTablet {
    padding-inline: 34px;
  }

  @include onDesktop() {
    padding-inline: 135px;
  }
}

@mixin pageGrid {
  --columns: 2;
  display: grid;
  column-gap: 20px;
  grid-template-columns: repeat(var(--columns), 1fr);

  @include onTablet {
    --columns: 6;
    column-gap: 20px;
  }

  @include onDesktop {
    --columns: 12;
    column-gap: 30px;
  }
}

@mixin hoverLine {
  &::after {
    content: "";
    position: absolute;
    bottom: -3px;
    display: block;
    height: 1px;
    width: 100%;
    // background-color: $c-blue;

    transition: transform 0.3s ease 0s;
    transform: scale(0);
    transform-origin: left;
  }

  &:hover::after {
    transform: scale(1);
  }
}
